/** @jsx jsx */
import { jsx } from "theme-ui"
import Divider from "../elements/divider"
import Inner from "../elements/inner"
import Content from "../elements/content"
import SVG from "./svg"
import { UpDown, UpDownWide } from "../styles/animations"
// @ts-ignore
import MusicMDX from "../sections/music"
import MusicProjectsMDX from "../sections/musicprojects.mdx";

const Music = ({ offset, factor = 1 }: { offset: number; factor?: number }) => (
  <div>
    <Divider speed={0.1} offset={offset} factor={factor}>
      <UpDown>
        <SVG icon="headphones" width={300} color="icon_orange" left="44%" top="-10%" />
      </UpDown>
    </Divider>
    <Content speed={0.4} offset={offset} factor={factor}>
      <Inner>
        <MusicMDX />
        <div
          sx={{
            display: `grid`,
            gridGap: [4, 4, 4, 5],
            gridTemplateColumns: [`1fr`, `1fr`, `repeat(2, 1fr)`],
            h2: { gridColumn: `-1/1`, color: `white !important` },
          }}
        >
          <MusicProjectsMDX />
        </div>
      </Inner>
    </Content>
  </div>
)

export default Music
